import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
const API_URL = "http://localhost:1337";
import { HttpClient } from '@angular/common/http';

@Injectable()
export class GameService {

  constructor(private http: HttpClient) { }
  getFinishedGame(gameId: string): Observable<any> {
    return this.http.get(API_URL + "/api/finished-game/"+gameId);
  }

  roll(data: any): Observable<any> {
    return this.http.post(API_URL + "/api/game/move", data);
  }
  start(data: any): Observable<any> {
    return this.http.post(API_URL + "/api/game/start", data);
  }
  setGameCookie(data: any) {
    return window.localStorage.setItem("game", JSON.stringify(data));
  }
  getGameCookie() {
    let game = window.localStorage.getItem("game");
    if (game) {
      return JSON.parse(game);
    } else {
      return false;
    }

  }
}



